*{
	font-family: Helvetica;
	margin:0;
	padding:0;
	font-weight: bolder;
	text-align: center;
}
button{
	cursor: pointer;
	color: #FFFFFF;
	font-size: 25px;
	border-radius: 11px;
	border: none;

}
button[name = "add-button"]{
	background-color:#f8a917;
	height:70px;
	width:125px;
	transition: all 0.35s;
	transform-origin: (0,0);
}
button[name = "add-button"]:active{
	background-color:#D48C01;
}
button[name = "remove-button"]{
	background-color: #e64d44;
	height:70px;
	width:62.5px;
}
button[name = "remove-button"]:active{
	background-color: #B20101;
}
.countProduct{
	background-color: #f8a917;
	color: #FFFFFF;
	display: inline-block;
	border-radius: 25px;
	height: 25px;
	width: 25px;
	font-size: 11px;
	font-weight: bolder;
	margin: 0px;
	line-height: 25px;
	margin-left: 45px;
	margin-bottom: -40px;
	text-align: center;
}